import kButton from '../../components/kun-button/kun-button.vue'
import KTitle from '../../components/kun-title/kun-title.vue'

export default {
	data() {
		return {
			loading:false,
			icon:''
		};
	},

	components:{
		kButton,
		KTitle
	},

	mounted() {
		console.log(this)
	},
	methods:{
		enterLoading(){
			console.log('click!')
			this.loading = true
			this.icon = 'fa-spinner'
			setTimeout(() => {
			  this.loading = false
			  this.icon = ''
			}, 2000)
		}
	}
}

